.bin-modal {
    &__overlay {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.5);
      backdrop-filter: blur(5px);
      display: flex;
      justify-content: center;
      align-items: center;
      z-index: 50;
    }
  
    &__content {
      background-color: white;
      border-radius: 0.5rem;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
      padding: 1.5rem;
      position: relative;
      overflow: auto;
    }
  
    &__close-button {
      position: absolute;
      top: 0.5rem;
      right: 0.5rem;
      background: none;
      border: none;
      cursor: pointer;
      color: #6b7280;
      transition: color 0.2s;
  
      &:hover {
        color: #374151;
      }
    }
  
    &__title {
      font-size: 1.25rem;
      font-weight: bold;
      margin-bottom: 1rem;
    }
  
    &__section {
      margin-bottom: 1.5rem;
  
      &-title {
        font-size: 1.125rem;
        font-weight: 600;
        margin-bottom: 0.5rem;
      }
  
      &--covered {
        .bin-modal__section-title {
          color: #10b981;
        }
      }
  
      &--uncovered {
        .bin-modal__section-title {
          color: #ef4444;
        }
      }
    }
  
    &__table {
      width: 100%;
      border-collapse: collapse;
  
      th, td {
        border: 1px solid #e5e7eb;
        padding: 0.5rem 1rem;
        text-align: left;
      }
  
      th {
        font-weight: 600;
      }
  
      tr:hover {
        background-color: #f3f4f6;
      }
    }
  
    &__table--covered {
      th {
        background-color: #d1fae5;
      }
    }
  
    &__table--uncovered {
      th {
        background-color: #fee2e2;
      }
    }
  }

  .button-container {
    width: 620px;
    position: fixed;
    height: 30px;
  }

  .content-container {
    height: 100%;
    width: 100%;
  }