// Card.scss
.card {
  background-color: white;
  border-radius: 8px;
  padding: 1.5rem;
  // box-shadow: 2px 1px 1px red;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 12px 28px 0px, rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(255, 255, 255, 0.05) 0px 0px 0px 1px inset;
  min-width: 18rem;
  min-height: 16rem;
  display: flex;
  flex-direction: column;
  
  .card-content {
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
  }

  .title-block {
    display: flex;
    align-items: center;
    // flex-direction: column;
    gap: 0.75rem;

    .title {
      font-weight: 600;
      color: #333;
    }

    .value-button {
      background: transparent;
      border: none;
      color: black;
      font-size: 1rem;
      padding: 0.4rem 0.8rem;
      border-radius: 6px;
      cursor: pointer;
      transition: all 0.2s ease;
      position: relative;
      overflow: hidden;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        background: rgba(255, 165, 0, 0.1);
        border-radius: inherit;
        transform: translate(-50%, -50%) scale(0);
        transition: transform 0.2s ease;
      }

      &:hover {
        color: #fd5002;
        transform: translateY(-1px);
        border: 1px solid #fd5002;
        &::before {
          transform: translate(-50%, -50%) scale(1);
        }
      }

      &:active {
        transform: translateY(1px);
        color: #fd5002;

        &::before {
          background: rgba(255, 165, 0, 0.2);
        }
      }

      &:focus {
        outline: none;
      }
    }
  }
}