.datapanel {
    position: relative;
    background: white;
    margin: 1rem;
    border-radius: 4px;
    padding: 20px;
    height: 100vh;
    width: 97.5vw;
    overflow-y: auto;
}

.dropdown-panels {
    display: flex;
    flex-direction: column;
    gap: 1rem; // Use gap instead of column-gap for better browser support
}

.datapanel {
    .dropdown-panels {
      > .dropdown {
        > .dropdown__content {
          width: 100%;
        }
      }
    }
  }